.reviews {
  position: relative;

  margin: 57px auto;

  .container {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: -50px;
      left: 0;

      width: 100%;
      height: 6px;

      background-image: url("../img/bg-zigzag-line.svg");
      background-size: contain;
      background-repeat: repeat-x;
      background-position: center top;
    }
  }
}

.reviews__item {
  margin: 24px 0 30px;
}

.reviews__title {
  @include h2();
}

.reviews__text {
  margin-bottom: 28px;

  line-height: $largLineHeght;
}

.reviews__author {
  font-size: $baseFontSize + 2;
  font-style: inherit;
}

.reviews__author-name {
  color: $black;
}

.reviews__author-mail {
  line-height: $largLineHeght;
}

.reviews__controls {
  display: flex;
}

.reviews__arrow {
  position: relative;

  flex-grow: 1;
  margin-bottom: 38px;
  padding: 0;
  padding-top: 4px;
  height: 52px;

  background-color: $white;
  border: 1px solid $white-four;

  cursor: pointer;

  &--right {
    border-left: none;
  }

  &:hover {
    svg {
      opacity: 0.6;
    }
  }

  &:active,
  &:focus {
    svg {
      opacity: 0.3;
    }
  }
}

.reviews__write-button {
  font-size: $baseFontSize + 3;
}

@media (min-width: $tablet-width) {
  .reviews {
    margin: 22px auto;
    margin-bottom: 90px;

    .container {
      background-image: url("../img/review@1x.jpg");
      background-repeat: no-repeat;
      background-position: 0 0;

      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/review@2x.jpg");
        background-size: 350px auto;
      }

      &::after {
        bottom: -85px;

        height: 15px;
      }
    }
  }

  .reviews__wrapper {
    position: relative;

    display: flex;
    flex-wrap: wrap;
    padding-top: 76px;
    min-height: 456px;
  }

  .reviews__write-button {
    margin-top: 3px;
    margin-right: -2px;
    max-height: 52px;
    max-width: 175px;
    order: -1;

    font-size: $baseFontSize + 2;
  }

  .reviews__title {
    flex-grow: 1;
    order: -2;
    margin-left: -7px;

    font-size: $h2tabletFontSize;
    line-height: $h2tabletLineHeight;
  }

  .reviews__item {
    margin: 45px 0;
    margin-left: -3px;
  }

  .reviews__text {
    font-size: $baseFontSize + 2;
    line-height: 30px;
  }

  .reviews__author {
    display: inline-block;
    margin-top: 30px;
    max-width: 254px;
  }

  .reviews__author-name {
    display: inline-block;
    margin-bottom: 3px;

    font-size: $baseFontSize + 3;
  }

  .reviews__controls {
    position: absolute;
    right: 0;
    bottom: 48px;

    display: block;

    .reviews__arrow {
      margin: 0 17px;
      width: 50px;

      border: none;
    }
  }
}

@media (min-width: $desctop-width) {
  .reviews {
    margin-top: 27px;

    .container {
      background-position: 50px 0;

      &::after {
        left: 52px;
        bottom: -90px;

        width: 1050px;
        height: 23px;
      }
    }
  }

  .reviews__wrapper {
    padding: 70px 2px 51px 170px;
  }

  .reviews__title {
    margin-left: 2px;

    font-size: $h2desctopFontSize;
    line-height: $h2desctopLineHeight;
  }

  .reviews__write-button {
    margin-top: 9px;
  }

  .reviews__item {
    margin: 45px 0;
  }

  .reviews__text {
    margin-left: 5px;
    max-width: 772px;
  }

  .reviews__author {
    margin-top: 10px;
    margin-left: 6px;
  }

  .reviews__author-name {
    font-size: $baseFontSize + 2;
  }

  .reviews__controls {
    right: 2px;
    bottom: 98px;
  }
}
